import { h } from 'preact';
import style from './style.css';
import Json from '../../assets/json/header.json';

const HeaderJson = { data: Json }

const Language = HeaderJson.data.HeaderJson.menuLanguage.map((item) => {
	return (
		<a class="dropdown-item" href={item.href} data-language={item.data}>
			<img class="langimg mr-2" src={item.image} alt="flag" />
			<span class="font-small-3">{item.name}</span>
		</a>
	)
});

const DropdownUser = HeaderJson.data.user.dropdownUser.map((item) => {
	return (
		<a class="dropdown-item" href={item.href}>
			<div class="d-flex align-items-center">
				<i class={item.icon}> </i><span>{item.item}</span>
			</div>
		</a>
	)
});
