import { h } from 'preact';
import style from './article.css';
import Json from '../../../assets/json/main.json';

const MainJson = { data: Json }

const CardStats = MainJson.data['card-widgetlineChart-info'].map((item) => {
    return (
        <div class="col-xl-3 col-lg-6 col-md-6 col-12">
            <div class={item.color}>
                <div class="card-content">
                    <div class="card-body py-0">
                        <div class="media pb-1">
                            <div class="media-body white text-left">
                                <h3 class="font-large-1 white mb-0">{item.value}</h3>
                                <p>{item.name}</p>
                            </div>
                            <div class="media-right white text-right">
                                <i class={item.icon}> </i>
                            </div>
                        </div>
                    </div>
                    <div id={item.chart} class="height-75 WidgetlineChart WidgetlineChartshadow mb-2"> </div>
                </div>
            </div>
        </div>
    )
});

const ProductSale = MainJson.data.ProductSale['Products-Sales'].map((item) => {
    return (
        <>
            <span class={item.color} style="width:15px; height:15px;"> </span>
            {item.name}
        </>
    )
});
const ShoppingCartHeader = MainJson.data.shoppingCart['card-title'].map((item) => {
    return (
        <>
            <th>{item}</th>
        </>
    )
});
const ShoppingCartBody = MainJson.data.shoppingCart['Card-body'].map((item) => {
    return (
        <tr>
            <td> <img class="height-50" src={item.image_product} alt="Generic placeholder image" /> </td>
            <td>{item.name}</td>
            <td>{item.quanity}</td>
            <td>
                <span class={item.statuscss}>{item.status}</span>
            </td>
            <td>{item.amount}</td>
            <td>
                <i class="ft-trash-2"> </i>
            </td>
        </tr>
    )
});

const VisitSalesStatistics = MainJson.data['visit-sales-statis']['card-case-info'].map((item) => {
    return (

        <span class="text-uppercase mr-3"> <i class={item.color}> </i> {item.name}</span>

    )
});

const StatiscticsHobbies = MainJson.data['Statisctics-Hobbies'].items.map((item) => {
    return (

        <div class="col text-center">
            <span class={item.color} style="width:10px; height:10px;"> </span>
            <span class="font-medium-4 d-block mb-2">{item.number}</span>
            <span class="font-small-3">{item.name}</span>
        </div>

    )
});
const UserList = MainJson.data.userlist['userlist-body'].map((item) => {
    return (
        <div class="media py-2">
            <img class="mr-3 avatar" src={item.image} alt="Avatar" width="35" />
            <div class="media-body">
                <h4 class="font-medium-1 mb-0">{item.name}</h4>
                <p class="text-muted font-small-3 m-0">{item.job}</p>
            </div>
            <div class="mt-1">
                <div class="checkbox">
                    <input type="checkbox" id="dash-checkbox1" checked="" />
                    <label for="dash-checkbox1"> </label>
                </div>
            </div>
        </div>

    )
});
const ProjectStats = MainJson.data['project-stats'].items.map((item) => {
    return (
        <div class="col-md-6 mb-3">
        <span class="mb-1 text-muted d-block">{item.value}% - {item.name}</span>
        <div class="progress" style="height: 5px;">
            <div class="progress-bar bg-info" role="progressbar" style={item.style} aria-valuenow={item.value} aria-valuemin="0" aria-valuemax="100">                               </div>
        </div>
    </div>
    )
});
